import React, { Component } from 'react';
import logo from './logo.svg';
import SignIn from './SignIn';
import Churches from './Churches';
import Responses from './Responses';
import TabMenu from './TabMenu';
import './App.css';
import { adminAuthUid, denominations, regions, phases, getPhaseId, averageResponse, getChurchPhase } from './constants'
import firebase from 'firebase'
import moment from 'moment'

var config = {
  apiKey: "AIzaSyAlQqNRvE3_6gGG7n4Lex5QftQoZ076ygk",
  authDomain: "unstuck-app.firebaseapp.com",
  databaseURL: "https://unstuck-app.firebaseio.com",
  projectId: "unstuck-app",
  storageBucket: "unstuck-app.appspot.com",
  messagingSenderId: "516397577712"
};
firebase.initializeApp(config);
var db = firebase.database();


class App extends Component {
  componentDidMount(){
    this.setState({
      auth: null,
      churches: null,
      responses: null,
      currentTab: "churches"
    })

    firebase.auth().onAuthStateChanged((user)=>{
      if (user) {
        this.setState({ auth: user })
        db.ref('church').once('value')
        .then((churchSnapshot) => 
          this.setState({ churches: churchSnapshot.val() })
        )
        db.ref('response').once('value')
        .then((responseSnapshot) =>
          this.setState({ responses: responseSnapshot.val() })
        )
      } else {
        this.setState({ auth: null })
      }
    })
  }
  signOut(){
    firebase.auth().signOut()
  }
  signIn(email, password){
    firebase.auth().signInWithEmailAndPassword(email, password)
    .catch(function(error) {
      //var errorCode = error.code;
      //var errorMessage = error.message;
    })
  }
  setTab(tabName){
    this.setState({ currentTab: tabName })
  }
  downloadCsv(csvContent, name){
    var encodedUri = encodeURI(csvContent);
    var link = document.createElement("a");
    link.setAttribute("href", encodedUri);
    link.setAttribute("download", name + ".csv");
    document.body.appendChild(link); // Required for FF

    link.click();
  }
  downloadIndividualCSV(){
    var csvContent = "data:text/csv;charset=utf-8,";
    var headerRow = ["Email", "Phase", "TimeCreated"];
    csvContent += headerRow.join(', ') + "\n";
    for(var responseId in this.state.responses){
      var r = this.state.responses[responseId];
      if(r.surveyData.questions){
        moment(r.surveyData.timeSubmitted).format("MMM Do YYYY");  
        var phase = getPhaseId(r.surveyData.questions)
        var row = [
          r.email, 
          phases[phase + 1], 
          moment(r.surveyData.timeSubmitted).format("MMM Do YYYY") 
        ];
        csvContent += row.map(x => (x && x.replace) ? x.replace(/,/g, "") : x).join(', ') + "\n" ;
      }
    }
    this.downloadCsv(csvContent, "UnstuckIndividualExport");
  }
  downloadChurchCSV(){
    var csvContent = "data:text/csv;charset=utf-8,";
    var headerRow = ["Name", "Phase", "Region", "Denomination", "AttendanceOne", "AttendanceTwo", "AttendanceThree", "GivingOne", "GivingTwo", "GivingThree", "Conversions", "TimeCreated"];
    csvContent += headerRow.join(', ') + "\n";
    for(var churchId in this.state.churches){
      var c = this.state.churches[churchId];
      var averageRes = averageResponse(c.responses)
      var questionData = averageRes.surveyData.questions
      var phase = getChurchPhase(c.churchData, questionData)

      var cd = c.churchData;
      var row = [cd.name, phases[phase.stage + 1], regions[cd.region],denominations[cd.denomination], cd.attendanceOne, cd.attendanceTwo, cd.attendanceThree, cd.givingOne, cd.givingTwo, cd.givingThree, cd.conversions, cd.timeCreated];
      csvContent += row.map(x => (x && x.replace) ? x.replace(/,/g, "") : x).join(', ') + "\n" ;
    }
    this.downloadCsv(csvContent, "UnstuckChurchExport");
  }
  render() {
    if(!this.state || !this.state.auth){
      return (<SignIn onSignIn={this.signIn} />)
    } 
    if(this.state.auth && this.state.auth.uid === adminAuthUid){
      return (
        <div className="App">
          <div className="App-header">
            <div className="container" style={{
              display: "flex",
              justifyContent: "space-between"
            }}>
              <img src={logo} className="App-logo" alt="logo" />  
              <p style={{ cursor: "pointer", marginTop: 7, fontWeight: 400 }} onClick={this.signOut}>Logout</p>
            </div>
          </div>
          <div className="App-intro container">
            <div style={{position: "absolute", top: 90, right: 20 }}>
              <p style={{ display: "inline", paddingRight: 10, cursor: "pointer", textDecoration: "underline", fontSize: 12}} onClick={this.downloadChurchCSV.bind(this)}>Church CSV</p>
              <p style={{ display: "inline", cursor: "pointer", textDecoration: "underline", fontSize: 12}} onClick={this.downloadIndividualCSV.bind(this)}>Individual CSV</p>
            </div>
            <TabMenu setTab={this.setTab.bind(this)} tab={this.state.currentTab}/>
            
            { this.state.currentTab === "churches" ? 
              <Churches churches={this.state.churches} /> : 
              <Responses responses={this.state.responses} />}
          </div>
        </div>
      );
    }

    return (<div style={{ 
        textAlign: "center",
        paddingTop: 100
      }}>
        <h4>This user is not an admin.</h4>
        <p onClick={this.signOut}>Logout</p>
      </div>)
  }
}

export default App;



